<template>
  <div class="tingcontent">
    <div class="up" @click="$router.go(-1)">
      <svg
        t="1664936691228"
        class="icon"
        viewBox="0 0 1024 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="27497"
        width="48"
        height="48"
      >
        <path
          d="M641.28 278.613333l-45.226667-45.226666-278.634666 278.762666 278.613333 278.485334 45.248-45.269334-233.365333-233.237333z"
          p-id="27498"
          fill="#ffffff"
        ></path>
      </svg>
    </div>
    <van-image
      v-if="tinglist?.coverImgUrl"
      class="logo"
      lazy-load
      :src="chuli(tinglist?.coverImgUrl)"
    />
    <div class="listen-detail">
      <p class="title">{{ tinglist?.title }}</p>
      <div class="artInfo">
        <div class="init-code">
          <div class="img">
            <van-image
              v-if="tinglist?.authorAvatar"
              class="zuozhelogo"
              lazy-load
              :src="chuli(tinglist?.authorAvatar)"
            />
          </div>
        </div>
        <div class="artContent">
          <p class="authorName">{{ tinglist?.authorNickName }}</p>
          <p class="detailInfo">
            <span class="num">
              <span
                >阅读{{
                  tinglist?.pageView > 10000
                    ? tinglist?.pageView > 100000000
                      ? (tinglist?.pageView / 100000000).toFixed(2) + "亿"
                      : (tinglist?.pageView / 10000).toFixed(2) + "万"
                    : tinglist?.pageView
                }}</span
              ></span
            >
            <span class="time">{{ tinglist?.releaseAt }}</span>
          </p>
        </div>
        <div class="btnempty"></div>
      </div>
    </div>
    <div class="listenContent">
      <div class="for" v-for="(f, index) in tinglist?.datas" :key="index">
        <div class="rich-text" v-html="f.richIntro"></div>
        <div class="listwrap">
          <div
            class="albumWrap"
            @click="$router.push({ path: `/album/${item.id}` })"
            v-for="item in f.albums"
            :key="item.id"
          >
            <div class="trackimg">
              <img src="@/assets/xiangqinglogo.png" alt="" />
              <div class="pic">
                <van-image
                  v-if="item?.albumInfo?.coverPath"
                  class="zuozhelogo"
                  lazy-load
                  :src="chuli(item?.albumInfo?.coverPath)"
                />
              </div>
            </div>
            <div class="infos">
              <p class="titles">{{ item?.albumInfo?.title }}</p>
              <p class="subtitle">{{ item?.albumInfo?.customTitle }}</p>
              <p class="numinfo">
                <i class="albumicon"></i>
                <span class="Y_T">{{
                  item?.statCountInfo?.playCount > 10000
                    ? item?.statCountInfo?.playCount > 100000000
                      ? (item?.statCountInfo?.playCount / 100000000).toFixed(2) + "亿"
                      : (item?.statCountInfo?.playCount / 10000).toFixed(2) + "万"
                    : item?.statCountInfo?.playCount
                }}</span>
                <i class="jiicon"></i>
                <span class="Y_T">{{ item?.statCountInfo?.trackCount }}</span>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tinglist: null,
    };
  },
  beforeRouteUpdate(to, from, next) {
    console.log("路由变化");
    this.axios
      .get(`https://apis.netstart.cn/ximalaya/ting/querySpecialDetail/${to.query.id}`)
      .then((res) => (this.tinglist = res.data.data));
    next();
  },
  created() {
    this.axios
      .get(
        `https://apis.netstart.cn/ximalaya/ting/querySpecialDetail/${this.$route.query.id}`
      )
      .then((res) => (this.tinglist = res.data.data));
  },
  methods: {
    chuli: function (n) {
      if (n?.startsWith("http")) {
        return n;
      } else if (!n?.startsWith("http")) {
        return `https://imagev2.xmcdn.com/${n}`;
      } else {
        return "1.png";
      }
    },
  },
};
</script>

<style lang="less" scoped>
.up {
  position: absolute;
  top: 10rem;
  left: 10rem;
  z-index: 100;
  svg {
    width: 30rem;
    height: 30rem;
  }
}
.tingcontent {
  .logo {
    width: 100%;
  }
  .listen-detail {
    .title {
      font-size: 24rem;
      line-height: 33rem;
      color: #000;
      margin: 22rem 16rem 0 16rem;
      font-weight: 700;
      margin-top: 18rem;
    }
    .artInfo {
      display: flex;
      height: 58rem;
      margin: 13rem 17rem 0;
      background: #fff;
      box-shadow: 0 2rem 20rem 0 rgb(0 0 0 / 5%);
      border-radius: 4rem;
      justify-content: space-between;
      .init-code {
        position: relative;
        .img {
          width: 22rem;
          height: 22rem;
          border-radius: 50%;
          overflow: hidden;
          flex: none;
          margin: 18rem 12rem 0;
        }
      }
      .artContent {
        flex: none;
        width: 65%;
        box-sizing: border-box;
        padding-top: 11rem;

        .authorName {
          font-size: 14rem;
          font-weight: 500;
          color: #333;
          line-height: 20rem;
        }
        .detailInfo {
          font-size: 12rem;
          font-weight: 400;
          color: #999;
          line-height: 17rem;
          .num {
            margin-right: 8rem;
          }
        }
      }
      .btnempty {
        width: 68rem;
      }
    }
  }
  .listenContent {
    margin: 10px 20px 0;
    .for {
      .rich-text {
        font-size: 16px;
        color: #836464;
        line-height: 30px;
        font-weight: 400;
        text-align: justify;
        hyphens: auto;
      }
      .listwrap {
        margin: 20px 0;
        .albumWrap {
          display: flex;
          height: 80px;
          background: #f3f3f3;
          position: relative;
          margin-top: 10px;
          align-items: center;
          .trackimg {
            width: 65px;
            height: 52px;
            position: relative;
            margin: 0 0 0 8px;
            flex: none;

            img {
              position: absolute;
              right: 0;
              width: 15px;
              height: 52px;
            }
            .pic {
              width: 49px;
              height: 100%;
              position: absolute;
              top: 0;
              left: 0;
            }
          }
          .infos {
            width: 80%;
            min-width: 49%;
            padding-left: 15px;
            .titles {
              font-size: 16px;
              line-height: 22px;
              color: #000;
              margin: 4px 8px 4px 0;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            .subtitle {
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            .numinfo {
              font-size: 12px;
              line-height: 17px;
              color: #999;
              letter-spacing: 0;
              height: 17px;
              margin: 4px 0;
              white-space: nowrap;
              .albumicon {
                width: 14px;
                height: 14px;
                display: inline-block;
                vertical-align: middle;
                margin-right: 5px;
                background: url(@/assets/ting/erji.png);
                background-size: 100%;
              }
              .Y_T {
                vertical-align: middle;
                display: inline-block;
              }
              .jiicon {
                background: url(@/assets/ting/sound.png) no-repeat;
                background-size: 100%;
                margin-left: 10px;
                width: 14px;
                height: 14px;
                display: inline-block;
                vertical-align: middle;
                margin-right: 5px;
              }
            }
          }
        }
      }
    }
  }
}
</style>
